/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --secondary-gradient: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  --accent-gradient: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  --success-gradient: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
  --dark-bg: #1a1a2e;
  --light-bg: #f8fafc;
  --text-dark: #2d3748;
  --text-light: #ffffff;
  --shadow-lg: 0 20px 40px rgba(0, 0, 0, 0.1);
  --shadow-xl: 0 25px 50px rgba(0, 0, 0, 0.15);
}

body {
  font-family:
    "Inter",
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    sans-serif;
  line-height: 1.7;
  color: var(--text-dark);
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
  overflow-x: hidden;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

/* Container */
.App {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Floating animations */
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes pulse-glow {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes blob {
  0% {
    transform: translate(0px, 0px) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

/* Section Styles */
section {
  padding: 100px 20px;
  margin-bottom: 40px;
  border-radius: 24px;
  position: relative;
  overflow: hidden;
  animation: fadeInUp 0.8s ease-out;
  animation-fill-mode: both;
}

section:nth-child(1) {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  animation-delay: 0.1s;
}

section:nth-child(2) {
  background: white;
  box-shadow: var(--shadow-lg);
  animation-delay: 0.2s;
}

section:nth-child(3) {
  background: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
  animation-delay: 0.3s;
}

section:nth-child(4) {
  background: var(--accent-gradient);
  color: white;
  animation-delay: 0.4s;
}

section:nth-child(5) {
  background: white;
  box-shadow: var(--shadow-xl);
  animation-delay: 0.5s;
}

section:nth-child(6) {
  background: linear-gradient(135deg, #a1c4fd 0%, #c2e9fb 100%);
  animation-delay: 0.6s;
}

section:nth-child(7) {
  background: white;
  animation-delay: 0.7s;
}

section:nth-child(8) {
  background: linear-gradient(135deg, #d4fc79 0%, #96e6a1 100%);
  animation-delay: 0.8s;
}

/* Decorative elements */
.section-decor {
  position: absolute;
  border-radius: 50%;
  filter: blur(40px);
  opacity: 0.15;
  z-index: 0;
}

.section-decor-1 {
  width: 300px;
  height: 300px;
  background: var(--primary-gradient);
  top: -150px;
  right: -150px;
  animation: blob 20s infinite;
}

.section-decor-2 {
  width: 200px;
  height: 200px;
  background: var(--secondary-gradient);
  bottom: -100px;
  left: -100px;
  animation: blob 15s infinite reverse;
}

/* Button Styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 18px 36px;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  z-index: 1;
  min-width: 200px;
  text-align: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: 0.5s;
  z-index: -1;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: var(--primary-gradient);
  color: white;
  animation: pulse-glow 2s infinite;
}

.btn-primary:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 15px 30px rgba(102, 126, 234, 0.4);
}

.btn-secondary {
  background: white;
  color: var(--text-dark);
  border: 3px solid transparent;
  background-clip: padding-box;
  position: relative;
}

.btn-secondary::after {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  background: var(--secondary-gradient);
  border-radius: 16px;
  z-index: -1;
}

.btn-secondary:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(240, 147, 251, 0.3);
}

.btn-accent {
  background: var(--accent-gradient);
  color: white;
  background-size: 200% 200%;
  animation: gradientShift 3s ease infinite;
}

.btn-accent:hover {
  transform: translateY(-5px) rotate(-2deg);
  box-shadow: 0 15px 30px rgba(79, 172, 254, 0.4);
}

.btn-success {
  background: var(--success-gradient);
  color: white;
}

.btn-success:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(67, 233, 123, 0.4);
}

.btn-outline {
  background: transparent;
  color: white;
  border: 3px solid white;
}

.btn-outline:hover {
  background: white;
  color: var(--text-dark);
}

/* Floating action button */
.floating-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: var(--secondary-gradient);
  color: white;
  border: none;
  cursor: pointer;
  font-size: 24px;
  box-shadow: 0 10px 20px rgba(240, 147, 251, 0.3);
  animation: float 3s ease-in-out infinite;
  z-index: 1000;
  transition: all 0.3s;
}

.floating-btn:hover {
  transform: scale(1.1);
  animation: none;
}

/* Card styles */
.card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
  position: relative;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-15px);
  box-shadow: var(--shadow-xl);
}

.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--primary-gradient);
}

.card-hover-effect {
  position: relative;
  overflow: hidden;
}

.card-hover-effect::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 5px;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  border-radius: 100%;
  transform: scale(1, 1) translate(-50%);
  transform-origin: 50% 50%;
}

.card-hover-effect:hover::after {
  animation: ripple 1s ease-out;
}

@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5;
  }
  100% {
    transform: scale(100, 100);
    opacity: 0;
  }
}

/* Headings */
h1,
h2,
h3,
h4 {
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1.5rem;
  position: relative;
  display: inline-block;
}

h1 {
  font-size: 4rem;
  background: linear-gradient(135deg, #667eea, #764ba2, #f093fb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 5s ease infinite;
  background-size: 200% 200%;
}

h2 {
  font-size: 3rem;
  color: var(--text-dark);
}

h2::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 80px;
  height: 5px;
  background: var(--primary-gradient);
  border-radius: 3px;
}

/* List styles */
.feature-list {
  list-style: none;
  padding: 0;
}

.feature-list li {
  padding: 15px 0;
  padding-left: 40px;
  position: relative;
  animation: slideInLeft 0.5s ease-out;
  animation-fill-mode: both;
}

.feature-list li:nth-child(1) {
  animation-delay: 0.1s;
}
.feature-list li:nth-child(2) {
  animation-delay: 0.2s;
}
.feature-list li:nth-child(3) {
  animation-delay: 0.3s;
}
.feature-list li:nth-child(4) {
  animation-delay: 0.4s;
}

.feature-list li::before {
  content: "✓";
  position: absolute;
  left: 0;
  top: 15px;
  width: 28px;
  height: 28px;
  background: var(--success-gradient);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
}

/* Form styles */
.form-group {
  margin-bottom: 25px;
  animation: fadeInUp 0.6s ease-out;
}

.form-input {
  width: 100%;
  padding: 18px 20px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 16px;
  transition: all 0.3s;
  background: white;
}

.form-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-2px);
}

/* Mobile-first responsive design */
@media (max-width: 768px) {
  section {
    padding: 60px 15px;
    margin-bottom: 20px;
    border-radius: 20px;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 2rem;
  }

  .btn {
    width: 100%;
    padding: 16px 24px;
    font-size: 16px;
    margin-bottom: 10px;
  }

  .floating-btn {
    width: 60px;
    height: 60px;
    bottom: 20px;
    right: 20px;
    font-size: 20px;
  }

  .card {
    padding: 20px;
    margin-bottom: 20px;
  }
}

/* Tablet styles */
@media (min-width: 769px) and (max-width: 1024px) {
  section {
    padding: 80px 30px;
  }

  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.5rem;
  }
}

/* Loading animation */
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Scroll progress bar */
.scroll-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 0%;
  height: 4px;
  background: var(--primary-gradient);
  z-index: 9999;
  transition: width 0.3s;
}

/* Particle background */
.particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(102, 126, 234, 0.5);
  border-radius: 50%;
  animation: float 15s infinite linear;
}

/* Staggered animations */
.stagger-item {
  opacity: 0;
  animation: fadeInUp 0.6s ease-out forwards;
}

/* Tooltip */
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltip-text {
  visibility: hidden;
  width: 200px;
  background-color: var(--text-dark);
  color: white;
  text-align: center;
  border-radius: 8px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -100px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* Badge */
.badge {
  display: inline-block;
  padding: 6px 12px;
  background: var(--success-gradient);
  color: white;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  animation: pulse-glow 2s infinite;
}

/* Grid animations */
.grid-item {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.6s ease-out forwards;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-gradient);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #764ba2 0%, #667eea 100%);
}

/* Selection color */
::selection {
  background: rgba(102, 126, 234, 0.3);
  color: var(--text-dark);
}

/* Focus styles for accessibility */
:focus {
  outline: 3px solid rgba(102, 126, 234, 0.5);
  outline-offset: 2px;
}

:focus:not(:focus-visible) {
  outline: none;
}

/* Hero stats text color */
.hero-stats .stat-label,
.hero-stats .stat-number {
  color: white;
}

/* Print styles */
@media print {
  .btn,
  .floating-btn,
  .section-decor {
    display: none !important;
  }
}
